import {
    Table
} from 'antd'
import {
    LeftOutlined
} from '@ant-design/icons'
import {GoBackBtn} from '../../../../component/Button'


const Detail = props => {


    const date = props.data.created_at.split(' ')[0].split('-')
    const time = props.data.created_at.split(' ')[1].split(':')

    const numberFormat = (num) => {
        let stringfmt = num.toString();
        let mod3 = 0;
        let result=''
        for(let i = stringfmt.length-1;i>=0;i--){
            if(mod3===3){
                result+='.'
                mod3=0
            }
            mod3+=1;
            result+=stringfmt[i];
        }
        return result.split('').reverse().join('');
    }
    const columns = [
        {
            title:'S lng',
            key:1,
            width:'100px',
            render:(text,record)=>{
                return(
                    <p>{record.amount}</p>
                )
            }
        },
        {
            title:'Tn sn phm',
            key:2,
            width:'500px',
            render:(text,record)=>{
                return(
                    <p>{record.productName}</p>
                )
            }
        },
        {
            title:'Tng',
            key:3,
            render:(text,record)=>{
                return(
                    <p>{numberFormat(record.total_price)}</p>
                )
            }
        }
    ]
    return(
        <div
        >
            <GoBackBtn
                onClick={()=>{
                    props.removeContent()
                }}
            >
                <LeftOutlined 
                    style={{fontSize:'18px'}}
                />
            </GoBackBtn>
            <div style={{borderBottom:'1px solid #CFD8DC',margin:'20px 0px'}}></div>
            <table>
                <tbody>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>Ngy t hng:</td>
                        <td style={{fontWeight:600}}>{date[2]}/{date[1]}/{date[0]}</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>Gi t hng:</td>
                        <td style={{fontWeight:600}}>{time[0]} gi, {time[1]} Pht, {time[2].slice(0,2)} Giy</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>a ch:</td>
                        <td style={{fontWeight:600}}>{props.data.address.address}</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>Tn ngi nhn:</td>
                        <td style={{fontWeight:600}}>{props.data.address.full_name}</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>S in thoi:</td>
                        <td style={{fontWeight:600}}>{props.data.address.phone}</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>Hnh thc thanh ton:</td>
                        <td style={{fontWeight:600}}>{props.data.payment_type}</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>Trng thi:</td>
                        <td style={{fontWeight:600}}>{props.data.status}</td>
                    </tr>
                    <tr style={{height:'50px'}}>
                        <td style={{width:'200px'}}>Thnh tin:</td>
                        <td style={{fontWeight:600}}>{numberFormat(props.data.total)}</td>
                    </tr>
                </tbody>
            </table>
            <Table
                dataSource={props.data.products}
                columns={columns}
                pagination={{pageSize:'6'}}
            />
        </div>
    )
}
export default Detail